import Vue from 'vue';
import Router from 'vue-router';
import Home from './views/Home/Home.vue';
import ShopList from './views/Home/ShopList.vue';
import About from './views/About/About.vue';
import Search from './views/About/Search.vue';
import Geohash from './views/About/Geohash.vue';
import Order from './views/Order/Order.vue';
import My from './views/My/Viewport/My.vue';
import Setusername from './views/My/Setusername.vue';
import Address from './views/My/Viewport/Address.vue';
import Checket from './views/My/Viewport/Checket.vue';
import Add from './views/My/Viewport/Add.vue';
import Creas from './views/My/Creas.vue';
import Prop from './views/My/Prop.vue';
import Info from './views/My/Viewport/Info.vue';
import Myinfo from './views/My/Myinfo.vue';
import Newly from './views/My/Newly.vue';
import AddDetail from './views/My/AddDetail.vue';
import Login from './components/Login.vue';
import App from './App.vue';

Vue.use(Router)

export default new Router({
	routes: [{
		path: '/',
		name: 'App',
		component: App,
		redirect: "/about",
		children: [{
			path: '/home',
			name: 'home',
			component: Home,
		}, {
			path: '/about',
			name: 'about',
			component: About,
		}, {
			path: '/shoplist',
			name: 'shoplist',
			component: ShopList,
		}, {
			path: '/search/:id',
			name: 'search',
			component: Search,
		}, {
			path: '/geohash/:id',
			name: 'geohash',
			component: Geohash,
		}, {
			path: '/order',
			name: 'order',
			component: Order,
		}, {
			path: '/my',
			name: 'my',
			component: My,
			children: [{
				path: '/',
				name: 'prop',
				component: Prop,
			}, {
				path: '/my/info',
				name: 'info',
				component: Info,
				children: [{
					path: '/',
					name: 'myinfo',
					component: Myinfo,
				}, {
					path: '/my/info/setusername',
					name: 'setusername',
					component: Setusername,
				}, {
					path: '/my/info/address',
					name: 'Address',
					component: Address,
					children: [{
						path: '/',
						name: 'creas',
						component: Creas,
					}, {
						path: '/my/info/address/add',
						name: 'add',
						component: Add,
						children: [{
							path: '/',
							name: 'newly',
							component: Newly,
						}, {
							path: '/my/info/address/add/addDetail',
							name: 'addDetail',
							component: AddDetail,
						}],
					}],
				}],
			}],
		}, {
			path: '/login',
			name: 'login',
			component: Login,
		}, ]
	}, ]
})