<template>
	<div class="newly">
		<Headers :FlagShowa=false :FlagShowk=true :FlagShowc=false></Headers>
		<div class="newly_one">
			<div class="doct">
				<el-input v-model="setusername_inp1" @input="inp_set" placeholder="请填写您的姓名" clearable> ></el-input>
				<div v-if="not1"></div>
				<div class="font" v-else>请填写您的姓名</div>
				<router-link to="/my/info/address/add/addDetail">
					<el-input v-model="subpust" placeholder="小区/写字楼/学校等" clearable> ></el-input>
				</router-link>
				<el-input v-model="setusername_inp2" @input="inp_set1" placeholder="请填写详细送餐地址" clearable> ></el-input>
				<div v-if="not2"></div>
				<div v-else class="font">送餐地址太短了，不能识别</div>
				<el-input v-model="setusername_inp3" @input="inp_set2" placeholder="请填写能够联系到您的手机号" clearable> ></el-input>
				<div v-if="not3"></div>
				<div class="font" v-else>请输入正确手机号</div>
				<el-input v-model="setusername_inp4" placeholder="备用联系电话 (选填)" clearable> ></el-input>
			</div>
		</div>
		<div class="neke">
			<el-button type="success" @click="newadd" class="but">新增地址</el-button>
		</div>
	</div>
</template>

<script>
	import { Toast, Notify } from 'vant';
	import axios from 'axios'
	import Headers from '@/components/Headers.vue';
	export default {
		name: "newly",
		components: {
			Headers
		},
		data() {
			return {
				not1: true,
				not2: true,
				not3: true,
				setusername_inp1: "",
				setusername_inp2: "",
				setusername_inp3: "",
				setusername_inp4: "",
				subpust: "",
				phone: "",
				domobj: ""
			}
		},
		mounted() {
			this.subpust = JSON.parse(localStorage.getItem('address'))
		},
		methods: {
			inp_set() {
				if(this.setusername_inp1.length == "") {
					this.not1 = false;
				} else {
					this.not1 = true;
				}
			},
			inp_set1() {
				if(this.setusername_inp2.length > 4) {
					this.not2 = true;
				} else {
					this.not2 = false;
				}
			},
			inp_set2() {
				this.phone = this.setusername_inp3;
				if(!(/^1[3456789]\d{9}$/.test(this.phone))) {
					this.not3 = false
				} else {
					this.not3 = true
				}
			},
			newadd() {
				if(this.setusername_inp1 != "" && this.setusername_inp2 != "" && this.setusername_inp3 != "" && this.not1 == true && this.not2 == true && this.not3 == true) {
					this.domobj = JSON.parse(localStorage.getItem("user_id"))
//					console.log(this.domobj)
					axios.post("http://elm.cangdu.org/v1/users/" + this.domobj + "/addresses", {
						address: this.subpust,
						address_detail: this.setusername_inp2,
						geohash: JSON.parse(localStorage.getItem("geohash")),
						name: this.setusername_inp1,
						phone: this.setusername_inp3,
						poi_type: 0,
						sex: 1,
						tag: "公司",
						tag_type: 4
					}).then((res) => {
						if(res.data.success=='添加地址成功'){
							this.$router.push('/my/info/address')
						}
					})
				} else {
					Notify({
						message: '请先完善所有地址信息',
						color: 'white',
						background: 'rgb(231, 77, 77)'
					});
				}
			}

		}
	}
</script>

<style lang="scss">
	html,
	body {
		width: 100%;
		height: 100%;
	}
	
	.newly {
		width: 100%;
		height: 100%;
		background: whitesmoke;
		.newly_one {
			padding: 0.2rem;
			width: 94.6%;
			/*height: 5rem;*/
			background: white;
			.doct {
				display: block;
				width: 90%;
				margin: 0 auto;
				.font {
					margin-top: 0.15rem;
					color: red;
					font-size: 0.2rem;
				}
				.el-input__inner {
					background: whitesmoke;
					height: 0.75rem;
				}
				.el-input {
					background: whitesmoke;
					margin-top: 0.2rem;
					display: block;
				}
			}
		}
		.neke {
			width: 90%;
			margin: 0 auto;
			.but {
				width: 100%;
			}
		}
	}
</style>