<template>
	<div class="box">
		<Headers :FlagShowa=true :FlagShowb=false :FlagShowc=false></Headers>
		<div class="content">
			<div class="location">
				<div class="nl">
					<ul>
						<li>当前定位城市：</li>
						<li>定位不准时，请在城市列表里选择</li>
					</ul>
				</div>
				<div class="ss">
					<span>{{arr.name}}></span>
				</div>
			</div>
			<div class="item">
				<div class="pai">热门城市</div>
				<div class="city sout">
					<div v-for="(item,index) in list" :key="index" @click="setParams(item.id)">
						{{item.name}}
					</div>
				</div>
			</div>
			<div class="item" v-for="(item,index) in cher" :key="index">
				<div class="pai">{{item}}</div>
				<div class="city">
					<div v-for="(i,index) in info[item]" :key="index" @click="setParams(i.id)">
						{{i.name}}
					</div>
				</div>
			</div>
		</div>
	</div>

</template>

<script>
	import axios from 'axios'
	import Footer from '@/components/Foot.vue'
	import Headers from '@/components/Headers.vue'

	function rem() {
		document.documentElement.style.fontSize = document.documentElement.clientWidth / 7.5 + "px";
	}
	rem();
	window.onresize = rem;
	export default {
		components: {
			Headers,
			Footer
		},
		data() {
			return {
				arr: [],
				list: [],
				info: [],
				cher: []
			}
		},
		mounted() {
			axios.get("http://elm.cangdu.org/v1/cities?type=guess").then((res) => {
				this.arr = res.data
			})
			axios.get("http://elm.cangdu.org/v1/cities?type=hot").then((res) => {
				this.list = res.data
			})
			axios.get("http://elm.cangdu.org/v1/cities?type=group").then((res) => {
				this.info = res.data;
				this.cher = Object.keys(res.data).sort();
			})
		},
		methods: {
			setParams(id) {
				this.$router.push({
					path: `/search/${id}`
				})
				localStorage.setItem('pamid', JSON.stringify(this.$router.history.current.params.id))
			},
		},
	}
</script>

