<template>
	<div class="setusername">
		<Headers :FlagShowa=false :FlagShowb=false :FlagShowi=true></Headers>
		<div class="setusername_inp ser">
			<el-input v-model="setusername_inp" @input="inp_set" placeholder="输入用户名" class="inp" clearable> ></el-input>
		</div>
		<p class="red" :style="style_first">用户名长度必须在5到24位之间</p>
		<p class="gray" :style="style_last">用户只能修改一次 (5-24字符之间)</p>
		<div class="setusername_inp">
			<el-button type="primary" class="butn">确认修改</el-button>
		</div>
	</div>
</template>
<script>
	import Headers from '@/components/Headers.vue';
	export default {
		name: 'setusername',
		components: {
			Headers
		},
		data() {
			return {
				setusername_inp: "",
				style_first: {
					"display": "none"
				},
				style_last: {
					"display": "block"
				},
			}
		},
		methods: {
			inp_set() {
				if(this.setusername_inp.length < 5) {
					this.style_first = {
						"display": "block"
					}
					this.style_last = {
						"display": "none"
					}
				} else if(this.setusername_inp.length > 24) {
					this.style_first = {
						"display": "block"
					}
					this.style_last = {
						"display": "none"
					}
				} else {
					this.style_first = {
						"display": "none"
					}
					this.style_last = {
						"display": "block"
					}
				}
			}
		}

	}
</script>
<style lang="scss">
	html,
	body {
		width: 100%;
		height: 100%;
	}
	
	.setusername {
		width: 100%;
		height: 100%;
		background: whitesmoke;
		.setusername_inp {
			width: 95%;
			margin: 0 auto;
			.butn {
				width: 100%;
			}
		}
		.red {
			margin-left: 0.3rem;
			height: 0.3rem;
			color: red;
			font-size: 0.24rem;
		}
		.gray {
			margin-left: 0.3rem;
			height: 0.3rem;
			color: gray;
			font-size: 0.24rem;
		}
	}
</style>