<template>
	<div class="Info">
		<Headers :FlagShowa=false :FlagShowh=true :FlagShowc=false></Headers>
		<div class="bool" v-if="bool"><img src="../../assets/ba08573c9efd478b44a8ca2a5e0eb7afed4acf3e13c40-GSNrR5_fw658.gif" /></div>
		<div class="first" v-else>
			<div class="nove">
				<p><span>头像</span></p>
				<input type="file" class="colder" @change="uploadAvatar()" ref="inp">
				<div class="neve"><img class="imgs" :src="'https://elm.cangdu.org/img/'+imgs" /><i class="el-icon-arrow-right cjs"></i></div>
			</div>
			<router-link to="/my/info/setusername">
				<div class="nqve">
					<p><span>用户名</span></p><span class="mins"><i class="el-icon-arrow-right"></i></span></div>
			</router-link>
			<router-link to="/my/info/address">
				<div class="nqve">
					<p><span>收货地址</span></p><span class="mins"><i class="el-icon-arrow-right"></i></span></div>
			</router-link>
			<div class="greay"><span>账号绑定</span></div>
			<div class="nive">
				<p class="lol"><span><i class="el-icon-mobile-phone"></i>手机</span></p><i class="el-icon-arrow-right con"></i></div>
			<div class="greay"><span>安全设置</span></div>
			<div class="nive">
				<p class="lol"><span>登陆密码</span></p><span class="mm">修改<i class="el-icon-arrow-right"></i></span></div>
			<div class="kong"></div>
			<div class="butt"><button class="button" @click="btng()">退出登陆</button></div>
		</div>
	</div>
</template>

<script>
	import { Toast } from 'vant';
	import { Dialog } from 'vant';
	import axios from 'axios';
	import Headers from '@/components/Headers.vue';
	export default {
		name: 'myinfo',
		components: {
			Headers
		},
		data() {
			return {
				imgs: "",
				bool: true,
				user_id:JSON.parse(localStorage.getItem("user_id"))
			}
		},
		mounted() {
			axios.get("http://elm.cangdu.org/v1/user?user_id=" + this.user_id).then((res) => {
				this.imgs = res.data.avatar;
				setTimeout(() => {
					this.bool = false;
				}, 500)
			})
		},
		methods: {
			btng() {
				Dialog.confirm({
					title: '退出提示',
					message: '是否确认退出'
				}).then(() => {
					this.$store.commit('singOut')
					//this.$router.push('/login')
					Toast.loading({
						mask: true,
						message: '正在退出...'
					});
					setTimeout(() => {
						this.$router.push('/my')
					}, 3200)
				}).catch(() => {
					// on cancel
				});
			},
			uploadAvatar() {
				var inputs = this.$refs.inp;
//				 console.log(inputs.files[0])
				let Data = new FormData();
				Data.append('file', inputs.files[0])
				let headers = {
					headers: {
						'Content-Type': 'multipart/from-data'
					}
				}
				axios.post(`http://cangdu.org.:8001/eus/v1/users/${this.user_id}/avatar`, Data, headers).then((res) => {
					 console.log(res)
					this.imgs = res.data.image_path
					// console.log(this.$store.state.name.avatar)
				})
			},
		}
	}
</script>
