<template>
	<div class="login">
		<Headers :FlagShowa=false :FlagShowe=true :FlagShowc=false></Headers>
		<div class="bool" v-if="bool"><img src="../assets/83856467c1954dd7b38f9afac56e5cce_th.gif" /></div>
		<div v-else>
			<div class="neke">
				<el-input v-model="username" placeholder="账号" class="inp" clearable> ></el-input>
			</div>
			<div class="neke">
				<el-input v-model="password" placeholder="密码" class="inp" show-password> ></el-input>
			</div>
			<div class="neke sode">
				<div class="yan">
					<el-input v-model="captcha_code" placeholder="验证码" class="inp" clearable></el-input>
				</div>
				<div class="ma"><img :src="captcha_imges" /></div>
				<div class="kan"><span>看不清</span><span @click="commit">换一张</span></div>
			</div>
			<div class="span">
				<p>温馨提示:未注册过的账号，登陆时将自动注册</p>
				<p>注册过的用户可凭账号密码登陆</p>
			</div>
			<div class="neke cold">
				<el-button @click="submite" type="success" class="but">登陆</el-button>
			</div>
			<div class="cong"><span @click="chong">重置密码？</span></div>
		</div>

	</div>
</template>
<script>
	import { Toast, Notify } from 'vant';
	import axios from 'axios';
	import Headers from '@/components/Headers.vue';
	axios.defaults.withCredentials = true;
	export default {
		components: {
			Headers,
		},
		data() {
			return {
				username: "",
				password: "",
				captcha_imges: "",
				captcha_code: "",
				bool: true
			}
		},
		mounted() {
			axios.post('http://elm.cangdu.org/v1/captchas', {}).then((res) => {
				this.captcha_imges = res.data.code;
				setTimeout(() => {
					this.bool = false
				}, 1000)
			})
		},
		methods: {
			commit() {
				axios.post('http://elm.cangdu.org/v1/captchas', {}).then((res) => {
					this.captcha_imges = res.data.code;
				})
			},

			submite() {
				axios.post('http://elm.cangdu.org/v2/login', {
					username: this.username,
					password: this.password,
					captcha_code: this.captcha_code
				}).then((res) => {
					console.log(res);
					if(res.data.message == "用户名参数错误") {
						Notify({
							message: '请先填写登陆账户信息',
							color: 'white',
							background: 'rgb(231, 77, 77)'
						});
					} else if(res.data.message == '密码错误') {
						Toast.fail(res.data.message);
					} else if(res.data.message == '验证码不正确') {
						Toast.fail(res.data.message);
						axios.post('http://elm.cangdu.org/v1/captchas', {}).then((res) => {
							this.captcha_imges = res.data.code;
						})
					} else {
						this.$store.commit('user_id',res)
						Toast.loading({
							mask: true,
							message: '正在登陆...'
						});
						setTimeout(() => {
							this.$router.push('/my')
						}, 3200)
					}
				})
			},
			chong() {
				this.password = ""
			}

		},

	}

	function rem() {
		document.documentElement.style.fontSize = document.documentElement.clientWidth / 7.5 + "px";
	}
	rem();
	window.onresize = rem;
</script>

<style lang="scss">
	body,
	html {
		height: 100%;
		width: 100%;
	}
	
	.bool {
		height: 93%;
		width: 100%;
		background: white;
		img {
			margin-top: 3rem;
			width: 100%;
		}
	}
	
	.yan {
		background: white;
		width: 60%;
	}
	
	.kan {
		margin-left: 0.24rem;
		margin-top: 0.65rem;
		font-size: 0.26rem;
		display: flex;
		flex-direction: column;
		span:first-of-type {
			color: gray;
		}
		span:last-of-type {
			color: #1E90FF;
		}
	}
	
	.sode {
		display: flex;
	}
	
	.ma {
		margin-left: 0.2rem;
		margin-top: 1rem;
		display: flex;
		align-items: center;
		justify-content: center;
	}
	
	.span {
		margin-top: 0.66rem;
		width: 100%;
		color: red;
		font-size: 0.23rem;
		p {
			margin-left: 0.3rem;
		}
	}
	
	.login {
		background: whitesmoke;
		width: 100%;
		height: 100%;
		.neke {
			height: 1rem;
		}
		.el-button {
			margin: 0 auto;
			width: 97%;
		}
		.cold {
			display: flex;
			align-items: center;
			justify-content: center;
		}
		.el-input__inner {
			height: 1rem;
		}
		.cong {
			margin-left: 6rem;
			margin-top: 0.3rem;
			color: #1E90FF;
			font-size: 0.3rem;
		}
	}
</style>