<template>
	<div class="home">
		<Headers :FlagShowc=true :Mageb=this.mage></Headers>

		<div class="loding" v-if="bool">
			<img src="../../assets/ba08573c9efd478b44a8ca2a5e0eb7afed4acf3e13c40-GSNrR5_fw658.gif" alt="" />
		</div>
		<div class="box" v-else>
			<div class="loder">
				<mt-swipe :auto="4000">
					<mt-swipe-item>
						<div class="boxs">
							<div class="list swiper-slide" v-for="(item,index) in lists ">
								<img class="imgs" :src="'https://fuss10.elemecdn.com'+item.image_url">
								<span>{{ item.title }}</span>
							</div>
						</div>
					</mt-swipe-item>
					<mt-swipe-item>
						<div class="boxs">
							<div class="list swiper-slide" v-for="(item,index) in list ">
								<img class="imgs" :src="'https://fuss10.elemecdn.com'+item.image_url">
								<span>{{ item.title }}</span>
							</div>
						</div>
					</mt-swipe-item>
				</mt-swipe>
			</div>
			<div class="gtiv"></div>
			<div class="vonc"><span class="el-icon-s-shop"></span><span>附近商家</span></div>
			<van-list v-model="loading" :finished="finished" finished-text="没有更多了" @load="onLoad">
				<div class="cojs">
					<div class="List" v-for="(item,index) in Lists " @click="funted(index)">
						<router-link :to="'/shoplist?geohash='+$route.query.geohash+'&id='+item.id">
							<img class="img" :src="'https://elm.cangdu.org/img/'+item.image_path">
							<div class="cod">
								<p class="cold1"><span class="pp">品牌</span><span class="pp2 cand">{{ item.name }}</span><span class="sat">保准票</span></p>
								<p class="cold1">
									<span class="hert">
								<van-rate v-model="item.rating" readonly allow-half void-icon="star" void-color="#eee"/>
								</span>
									<span class="hert2">{{ item.rating }}</span>
									<span class="hert3">月售{{item.recent_order_num}}</span>
									<span class="hert4">蜂鸟专送</span>
									<span class="hert5">准时达</span>
								</p>
								<p class="cold1"><span>20元起送/配送费约￥5</span><span class="nn"><span>{{item.distance}}</span>&nbsp;/&nbsp;<span class="ss">{{item.order_lead_time}}</span></span>
								</p>
							</div>
						</router-link>
					</div>
				</div>
			</van-list>
		</div>
		<Footer></Footer>
	</div>
</template>

<script>
	import { Rate } from 'vant';
	import { List } from 'vant';
	import { Swipe, SwipeItem } from 'mint-ui';
	import Headers from '@/components/Headers.vue';
	import Footer from '@/components/Foot.vue';
	import axios from 'axios';

	function rem() {
		document.documentElement.style.fontSize = document.documentElement.clientWidth / 7.5 + "px";
	}
	rem();
	window.onresize = rem;

	export default {
		components: {
			Headers,
			Footer
		},

		data() {
			return {
				listc: [],
				error: false,
				loading: false,
				mage: "",
				list: [],
				lists: [],
				Lists: [],
				limit: 10,
				bool: true,
				finished: false
			}
		},
		methods: {
			onLoad() {
				// 异步更新数据
				if(this.limit += 10) {
					axios.get("http://elm.cangdu.org/shopping/restaurants?latitude=39.78493&longitude=116.49476&offset=0&limit=" + this.limit + "&extras[]=activities&keyword=&restaurant_category_id=&restaurant_category_ids[]=&order_by=&delivery_mode[]=").then(res => {
						this.Lists = res.data;
					})
					setTimeout(() => {
						// 加载状态结束
						this.loading = false;
						// 数据全部加载完成
						if(this.Lists.length > 90) {
							this.finished = true;
						}
					}, 1000);
				}
			},
			funted(index) {
				console.log(this.Lists[index])
				localStorage.setItem('shoplistimg', JSON.stringify(this.Lists[index]));
			}
		},
		mounted() {
			axios.get('http://elm.cangdu.org/v2/index_entry?geohash=39.78493,116.49476&group_type=1&flags[]=F').then(res => {
				this.lists = res.data.splice(0, 8);
			})
			axios.get('http://elm.cangdu.org/v2/index_entry?geohash=39.78493,116.49476&group_type=1&flags[]=F').then(res => {
				this.list = res.data.splice(8, 16);
			})
			axios.get('http://elm.cangdu.org/shopping/restaurants?latitude=39.78493&longitude=116.49476&offset=0&limit=20&extras[]=activities&keyword=&restaurant_category_id=&restaurant_category_ids[]=&order_by=&delivery_mode[]=').then(res => {
				this.Lists = res.data;
				//				console.log(res.data)
				setTimeout(() => {
					this.bool = false
				}, 2000)
			})
			let geohash = this.$route.query.geohash;
//			console.log(geohash)
			let hash = geohash.split(',');
			let geohash1 = hash[0];
			let geohash2 = hash[1];
			axios.get(`http://elm.cangdu.org/v2/pois/${geohash}`).then((res) => {
				this.mage = res.data;
			});
		},
	}
</script>
