			if(this.user_id == null) {
				localStorage.setItem('geohash', JSON.stringify("31.22299,121.36025"))
				this.xianshi = {
					"display": "none"
				}
				this.yingcang = {
					"display": "block"
				}
			} else {
				this.xianshi = {
					"display": "block"
				}
				this.yingcang = {
					"display": "none"
				}
			}
		},
