<template>
	<div class="shoplist">
		<div v-if="nummer" class="nummer">
			<van-skeleton title avatar :row="3" class="bottom" />
			<van-skeleton title avatar :row="3" />
			<van-skeleton title avatar :row="3" />
			<van-skeleton title avatar :row="3" />
			<van-skeleton title avatar :row="3" />
			<!--<img src="../../assets/ba08573c9efd478b44a8ca2a5e0eb7afed4acf3e13c40-GSNrR5_fw658.gif" />-->
		</div>
		<div v-else>
			<div class="fir">
				<i class="el-icon-arrow-left nave" @click="$router.go(-1)"></i>
				<img class="note" :src="'//elm.cangdu.org/img/'+listimg.image_path" />
				<div class="borr">
					<img class="imgss" :src="'//elm.cangdu.org/img/'+listimg.image_path" />
					<div>
						<p class="block">{{listimg.name}}</p>
						<p class="block2">商家配送/{{listimg.order_lead_time}}送达/{{listimg.piecewise_agent_fee.tips}}</p>
						<p class="block3">公告:{{listimg.promotion_info}}</p>
					</div>
				</div>
			</div>

			<van-tabs @click="onClick">
				<van-tab title="商品">
					<van-sidebar v-model="activeKey">
						<van-sidebar-item v-for="(item,index) in cli" :title="item.name" />
					</van-sidebar>
					<div id="right">
						<ul v-for="(item,index) in cli">
							<li class="numnn" v-for="(i,index) in item.foods">
								<img :src="'//elm.cangdu.org/img/'+i.image_path" />
								<div class="notes">
									<p class="p1">{{i.name}}</p>
									<p class="p2">{{i.description}}</p>
									<p class="p3"><span>月售{{i.month_sales}}份</span>&nbsp;&nbsp;<span>好评率{{i.satisfy_rate}}%</span></p>
									<p class="p4"><span>￥</span>{{i.specfoods[0].price}}<span>起</span><button class="gui" @click="add($event,index)">+</button></p>

								</div>
							</li>
						</ul>
					</div>
					<footer class="foot" @click="colock">
						<van-icon class="nand" name="shopping-cart nand" />
						<div class="fpo">
							<p>￥0.00</p>
							<p>配送费5元</p>
						</div>
						<button class="bott">还查20元起送</button>
					</footer>
				</van-tab>
				<van-tab title="评价">

				</van-tab>
			</van-tabs>
			<van-action-sheet v-model="show" title="购物车">
				<div v-for="(item,key) in cart" :key="key" class="node">
					<img class="imgg" :src="'//elm.cangdu.org/img/'+item.item_imgs" />
					<p class="mm1">{{item.item_name}}</p>
					<p class="mm3">{{item.item_price*item.num}}</p>
					<span class="mm2">
							<button @click="jianlist(key)">-</button>
								{{item.num}}
							<button @click="jialist(key)">+</button>
						</span>
				</div>
			</van-action-sheet>
		</div>
	</div>
</template>
<script>
	import axios from 'axios';
	import { Icon } from 'vant';
	import { Skeleton } from 'vant';
	import '../../static/theball/theBall.css'
	import PoSx from '../../static/theball/theBall.js'
	import { ActionSheet } from 'vant';
	export default {
		components: {

		},
		data() {
			return {
				shop: [],
				activeKey: 0,
				listimg: "",
				nummer: true,
				cli: [],
				show: false,
				num: 1,
				arr: null,
				cart: []
			}
		},
		mounted() {
			axios.get("http://elm.cangdu.org/shopping/v2/menu?restaurant_id=" + this.$route.query.id).then((res) => {
				this.shop = res.data;
				this.listimg = JSON.parse(localStorage.getItem("shoplistimg"))
				this.nummer = false
				this.cli = this.shop.__ob__.value
			})
			if(this.arr == JSON.parse(localStorage.getItem('cart'))) {
				localStorage.setItem('cart', JSON.stringify([]))
			}
			this.cart = JSON.parse(localStorage.getItem('cart'))
		},
		methods: {
			onClick(name, title) {
				this.$toast(title);
			},
			add(event, index) {
				PoSx(event);
//				console.log(this.$data.shop[0].foods[index].specfoods[0].price)
				var obj = {
					num: this.num,
					item_imgs: this.$data.shop[0].foods[index].image_path,
					item_id: this.$data.shop[0].foods[index].item_id,
					item_name: this.$data.shop[0].foods[index].name,
					item_price: this.$data.shop[0].foods[index].specfoods[0].price,
					
				}
					console.log(this.$data.shop[0].foods[index])
				this.cart.push(obj)
				localStorage.setItem('cart', JSON.stringify(this.cart))
			},
			colock() {
				this.cart = JSON.parse(localStorage.getItem('cart'))
				this.show = true
			},
			jialist(key) {
				this.cart[key].num++
					localStorage.setItem('cart', JSON.stringify(this.cart))
			},
			jianlist(key) {
				this.cart[key].num--
					if(this.cart[key].num <= 0) {
						this.cart.splice(key, 1)
						localStorage.setItem('cart', JSON.stringify(this.cart))
					} else {
						localStorage.setItem('cart', JSON.stringify(this.cart))
					}
			}
		}
	}

	function rem() {
		document.documentElement.style.fontSize = document.documentElement.clientWidth / 7.5 + "px";
	}
	rem();
	window.onresize = rem;
</script>
<style lang="scss">
	.van-popup--bottom {
		height: 7rem;
	}
	
	
	.node {
		height: 2rem;
		display: flex;
		align-items: center;
		width: 100%;
		font-size: 0.3rem;
		.mm3 {
			width: 2rem;
		}
		.mm1 {
			margin-left: 0.2rem;
			overflow: hidden;
			width: 2.2rem;
		}
		.imgg {
			width: 1.2rem;
		}
	}
	
	.bottom {
		margin-top: 1rem;
	}
	
	.foot {
		height: 1rem;
		position: absolute;
		bottom: -1rem;
		z-index: 999;
		background: rgba(22, 25, 20, 0.89);
		width: 100%;
		color: white;
		.fpo {
			p {
				padding: 0;
				margin: 0;
			}
			>p:first-of-type {
				font-size: 0.3rem;
			}
			position: absolute;
			font-size: 0.2rem;
			color: white;
			left: 1.9rem;
			top: 0.1rem;
		}
		.bott {
			font-weight: 600;
			width: 30%;
			font-size: 0.26rem;
			background: #696969;
			border: 0rem;
			position: absolute;
			height: 1rem;
			left: 5.25rem;
		}
		.nand {
			left: 0.3rem;
			background: rgba(22, 25, 20, 0.89);
			bottom: 0.2rem;
			position: absolute;
			width: 1rem;
			height: 1rem;
			display: flex;
			align-items: center;
			justify-content: center;
			border: 0.1rem solid rgba(56, 59, 51, 0.89);
			border-radius: 50%;
			font-size: 0.6rem;
			color: white;
			position: absolute;
		}
	}
	
	.shoplist {
		.van-tabs__line {
			background-color: #1E90FF;
		}
		.van-sidebar-item--select {
			border-color: #1E90FF;
		}
		.nave {
			position: absolute;
			top: 0.2rem;
			left: 0.2rem;
			color: white;
			z-index: 3;
			font-size: 0.5rem;
		}
		.gui {
			/*font-size: 0.37rem;*/
			width: 0.4rem;
			height: 0.4rem;
			margin-left: 2.1rem;
			font-size: 0.2rem;
			background: #1E90FF;
			color: white;
			border: 0rem;
			border-radius: 50%;
		}
		.nummer {
			width: 100%;
			height: 100%;
			img {
				position: absolute;
				top: 3rem;
				width: 100%;
			}
		}
		.notes {
			width: 100%;
		}
		.p1 {
			padding: 0;
			margin: 0.2rem;
			font-size: 0.3rem;
			font-weight: 600;
			width: 4rem;
			overflow: hidden;
		}
		.p2 {
			padding: 0;
			margin: 0.2rem;
			font-size: 0.24rem;
			color: gray;
		}
		.p3 {
			padding: 0;
			margin: 0.2rem;
			font-size: 0.25rem;
		}
		.p4 {
			padding: 0;
			/*margin: 0.2rem;*/
			font-size: 0.35rem;
			font-weight: 600;
			color: #FF8C00;
			span:first-of-type {
				font-size: 0.23rem;
				font-weight: 500;
			}
			span:last-of-type {
				margin-left: 0.2rem;
				font-size: 0.23rem;
				color: dimgrey;
				font-weight: 500;
			}
		}
		.numnn {
			display: flex;
			border-bottom: 0.001rem solid whitesmoke;
			img {
				margin-left: 0.2rem;
				margin-top: 0.2rem;
				height: 20%;
				width: 20%;
			}
		}
		.van-sidebar {
			overflow: auto;
			height: 9.45rem;
		}
		.van-tab__pane {
			display: flex;
		}
		.van-sidebar {
			background: white;
		}
		#right {
			width: 5.78rem;
			height: 9.45rem;
			background: white;
			overflow: auto;
		}
		.fir {
			background-color: rgba(119, 103, 137, .43);
			height: 2rem;
			width: 100%;
			overflow: hidden;
			.note {
				-webkit-filter: gray(50%);
				flex: 3;
				width: 100%;
				-webkit-filter: grayscale(50%);
				-moz-filter: blur(1px);
				-ms-filter: blur(1px);
				filter: blur(10px);
			}
		}
		.borr {
			display: flex;
			align-items: center;
			background: rgba(119, 103, 137, 0.43);
			width: 100%;
			height: 2rem;
			position: absolute;
			top: 0;
			flex: 999;
			font-size: 0.3rem;
			color: white;
			.imgss {
				margin-left: 0.2rem;
				padding: 0.1rem;
				width: 1.6rem;
			}
			.block {
				width: 5rem;
				overflow: auto;
				font-weight: 600;
				color: white;
			}
			.block2 {
				color: white;
				font-size: 0.23rem;
			}
			.block3 {
				color: white;
				font-size: 0.23rem;
			}
		}
	}
</style>