<template>
	<div class="geohash">
		<Headers :FlagShowa=false :FlagShowb=false :FlagShowc=false :FlagShowd=true :Mageb=this.mage></Headers>
		<div class="kibe">
			<div class="kids">
				<el-input v-model="value" placeholder="输入商家或美食名称" class="inp" clearable> ></el-input>
			</div>
			<div class="cide">
				<el-button type="primary" @click="add()" class="but">提交</el-button>
			</div>
		</div>
		<div class="blive" :style="commes">
			<div class="colde"><span>搜索历史</span></div>
			<div class="historys" v-for="(item,index) in info" :key="index">
				<div><span>{{item}}</span></div>
				<div @click="del(index)">
					<h3>x</h3></div>
			</div>
			<div class="search">
				<button class="van" @click="clear()" type="info">
					清除搜索历史</button>
			</div>
		</div>
		<div class="none">抱歉！ 无搜索结果</div>
		<Footer></Footer>
	</div>
</template>
<script>
	import axios from 'axios';
	import Headers from '@/components/Headers.vue';
	import Footer from '@/components/Foot.vue';
	export default {
		data() {
			return {
				value: '',
				localStr: [],
				info: [],
				mage:"",
				commes: {
					"display": "block"
				}
			}
		},
		components: {
			Headers,
			Footer
		},
		methods: {
			add() {
				let geohash = this.$route.params.id
				console.log(geohash)
				if(this.value) {
					axios.get(`http://elm.cangdu.org/v4/restaurants?extras[]=restaurant_activity&geohash=${geohash}&keyword=${this.value}&type=search`).then((res) => {
					})
				}
				let obj = [...this.info]
				let zhi = obj.some((v) => {
					return v == this.value
				})
				if(zhi == false && this.value == "") {

				} else {
					obj.push(this.value)
				}
				localStorage.setItem('localStr', JSON.stringify(obj))
				this.info = obj
				this.value = ''
				if(this.info.length != 0) {
					this.commes = {
						"display": "block"
					}
				} else if(this.info.length == 0) {
					this.commes = {
						"display": "none"
					}
				}
			},
			del(index) {
				var sot = [...this.info]
				sot.splice(index, 1)
				localStorage.setItem('localStr', JSON.stringify(sot))
				this.info = JSON.parse(localStorage.getItem('localStr'))
				if(this.info.length != 0) {
					this.commes = {
						"display": "block"
					}
				} else if(this.info.length == 0) {
					this.commes = {
						"display": "none"
					}
				}
			},
			clear() {
				this.info = []
				localStorage.removeItem('localStr')
				this.commes = {
					"display": "none"
				}
			}
		},
		mounted() {
			let obj = JSON.parse(localStorage.getItem('localStr'))
			if(obj != null) {
				this.info = obj;
			}
		},
	}

	function rem() {
		document.documentElement.style.fontSize = document.documentElement.clientWidth / 7.5 + "px";
	}
	rem();
	window.onresize = rem;
</script>
<style lang="scss">
	html,
	body,
	.geohash {
		width: 100%;
		height: 100%;
		#nav{
			background: white;
		}
	}
	
	.geohash {
		height: 100%;
		background: whitesmoke;
	}
	
	.blive {
		background: white;
		margin-top: 0.3rem;
		width: 100%;
		.colde {
			color: gray;
			width: 100%;
			height: 0.9rem;
			background: whitesmoke;
			display: flex;
			align-items: center;
			/*justify-content: center;*/
			font-size: 0.3rem;
			span {
				margin-left: 0.25rem;
			}
		}
	}
	
	.historys {
		width: 100%;
		display: flex;
		align-items: center;
		justify-content: space-around;
		font-size: 0.3rem;
		border-bottom: 0.01rem solid gainsboro;
	}
	
	.kibe {
		height: 1.5rem;
		background: white;
		width: 100%;
		margin-top: 0.3rem;
		display: flex;
		align-items: center;
		justify-content: space-around;
		.kids {
			margin-left: 0.2rem;
			width: 70%;
			display: flex;
			align-items: center;
			justify-content: center;
		}
		.cide {
			width: 30%;
			display: flex;
			align-items: center;
			justify-content: center;
		}
	}
	
	.search {
		font-weight: 500;
		/*background: whitesmoke;*/
		height: 0.8rem;
		color: #1E90FF;
		font-size: 0.3rem;
		width: 100%;
		display: flex;
		align-items: center;
		justify-content: center;
	}
	.van{
		width: 100%;
		height: 0.8rem;
		display: flex;
		align-items: center;
		justify-content: center;
		background: white;
		border: 0rem;
	}
	.none {
		color: gray;
		border-radius: 0.2rem;
		margin: 0 auto;
		margin-top: 0.4rem;
		height: 1rem;
		background: whitesmoke;
		display: flex;
		align-items: center;
		justify-content: center;
		font-size: 0.34rem;
		width: 90%;
	}
</style>