<template>
	<div class="addDetail">
		<Headers  :FlagShowl=true ></Headers>
		<div class="conde">
			<div class="nond">
				<el-input class="inp" v-model="setusername_inp" placeholder="请输入小区/写字楼/学校等 " clearable> ></el-input>
				<el-button type="primary" @click="motuds" class="but">确认</el-button>
			</div>
		</div>
		<div class="condes">为了满足商家的送餐要求，建议您从列表中选择地址</div>
		<div class="ppp" v-if="sets">
			<p>找不到地址？</p>
			<p>请尝试输入小区，写字楼或学校名</p>
			<p>详情地址 (如门牌号) 可稍后输入哦。</p>
		</div>
		<div class="goue"  v-else>
			<div class="regions" v-for="(item,index) in list" :key="index" @click="getParams(index)">
				<p>{{item.name}}</p>
				<p>{{item.address}}</p>
			</div>
		</div>
	</div>
</template>

<script>
	import Headers from '@/components/Headers.vue';
	import axios from 'axios';
	export default {
		name: "addDetail",
		components: {
			Headers
		},
		data() {
			return {
				setusername_inp: "",
				list:[],
				pamid:"",
				sets:true
			}
		},
		methods: {
			motuds(){
				this.pamid=JSON.parse(localStorage.getItem('pamid'))
				if(this.setusername_inp) {
					axios.get('http://elm.cangdu.org/v1/pois?type=search&city_id=' + this.pamid + '&keyword=' + this.setusername_inp).then((res) => {
						this.list = res.data
						this.sets=false
					})
			}
		},
		getParams(index){
			localStorage.setItem('address', JSON.stringify(this.$data.list[index].name));
			this.$router.push({
					path: "/my/info/address/add"
			})
		}
	}
	}
</script>

<style lang="scss">
	html,
	body {
		width: 100%;
		height: 100%;
	}
	
	.addDetail {
		width: 100%;
		height: 100%;
		background: whitesmoke;
		.conde {
			width: 100%;
			background: white;
		}
		.nond {
			height: 1.3rem;
			background: white;
			width: 95%;
			align-items: center;
			justify-content: space-around;
			display: flex;
			.inp {
				margin-left: 0.3rem;
			}
			.but {
				margin-left: 0.4rem;
			}
			.el-input__inner {
				background: whitesmoke;
			}
		}
		.condes {
			color: #FF8C00;
			height: 0.5rem;
			font-size: 0.27rem;
			display: flex;
			align-items: center;
			justify-content: center;
			background: antiquewhite;
		}
		.ppp {
			color: darkgray;
			margin-top: 3rem;
			font-size: 0.32rem;
			text-align: center;
		}
		.goue{
			height: 10.5rem;
			background: whitesmoke;
			overflow: auto;
		}
		.regions{
			border-bottom: 0.01rem solid gainsboro;
			margin: auto;
			background: whitesmoke;
			p{
				margin-left: 0.5rem;
				color: gray;
				font-size: 0.3rem;
				background: whitesmoke;
			}
		}
	}
</style>