<template>
	<div class="cosk">

		<div class="creas">
			<Headers class="first" @aa='bb($event)' :FlagShowj=true></Headers>
			<div class="dl"></div>
			<div class="elinc">
				<p>新增地址</p>
				<router-link to="/my/info/address/add"><i class="el-icon-arrow-right"></i></router-link>
			</div>
			<div v-for="(item,index) in addres" class="daares">
				<p><span>{{item.address}}</span><span class="spann">{{item.phone}}</span><span :style="{display:dian?'none':'inline'}" @click="DelData(item)">x</span></p>
			</div>
		</div>
	</div>
</template>
<script>
	import axios from "axios"
	import Headers from '@/components/Headers.vue';
	export default {
		name: 'creas',
		components: {
			Headers
		},
		data() {
			return {
				domobj: "",
				addres: [],
				click: {
					"display": "none"
				},
				dian: true
			}
		},
		mounted() {
			this.domobj = JSON.parse(localStorage.getItem("user_id"))
			axios.get("http://elm.cangdu.org/v1/users/" + this.domobj + "/addresses").then((res) => {
				this.addres = res.data;
			})
		},
		methods: {
			DelData(req) {
				var User_id = JSON.parse(localStorage.getItem('user_id'))
				axios.delete(`http://elm.cangdu.org/v1/users/${User_id}/addresses/${req.id}`).then((res) => {
					if(res.data.success == '删除地址成功') {
						axios.get("http://elm.cangdu.org/v1/users/" + this.domobj + "/addresses").then((res) => {
							this.addres = res.data;
						})
						this.$router.push("/my/info/address")
					}
				})
			},
			bb(res) {
				this.dian = res
			}
		}
	}
</script>
<style lang="scss">
	.cosk {
		height: 100%;
		background: whitesmoke;
	}
	
	.daares {
		margin-top: 0.2rem;
		height: 0.8rem;
		display: flex;
		align-items: center;
		justify-content: space-between;
		width: 100%;
		background: white;
		border-bottom: 0.01rem solid gainsboro;
		font-size: 0.3rem;
		p {
			margin: auto;
			width: 90%;
			padding: 0;
			margin: 0;
			span:first-of-type {
				margin-left: 0.2rem;
			}
			.spann {
				position: absolute;
				left: 4rem;
			}
			span:last-of-type {
				position: absolute;
				left: 7rem;
			}
		}
	}
	
	.creas {
		height: 100%;
		background: whitesmoke;
		Headers {
			background: #1E90FF;
		}
		.dl {
			width: 100%;
			height: 0.2rem;
			border-bottom: 0.01rem solid gainsboro;
		}
		.elinc {
			margin-top: 0.15rem;
			display: flex;
			align-items: center;
			background: white;
			border-bottom: 0.01rem solid gainsboro;
			font-size: 0.3rem;
			width: 100%;
			p {
				margin-left: 0.2rem;
				width: 6.8rem;
			}
		}
	}
</style>