<script>
	import { Image } from 'vant';
	import axios from 'axios';
	import Headers from '@/components/Headers.vue';
	import Footer from '@/components/Foot.vue';

	export default {
		name: 'prop',
		components: {
			Headers,
			Footer
		},
		data() {
			return {
				user_id: "",
				commitname: "",
				imgs: "",
				sp1: "",
				sp2: "",
				sp3: "",
				xianshi: {
					"display": "block"
				},
				yingcang: {
					"display": "none"
				}
			}
		},
		mounted() {
			this.user_id = JSON.parse(localStorage.getItem('user_id'))
			axios.get("http://elm.cangdu.org/v1/user?user_id=" + this.user_id).then((res) => {
//				console.log(res.data)
				this.commitname = res.data.username;
				this.imgs = res.data.avatar;
				this.sp1 = res.data.current_address_id + "." + res.data.current_invoice_id + res.data.delivery_card_expire_days
				this.sp2 = res.data.gift_amount;
				this.sp3 = res.data.point;
			})
			if(this.user_id == null) {
				this.xianshi = {
					"display": "none"
				}
				this.yingcang = {
					"display": "block"
				}
			}
		}
	}
</script>
