import Vue from 'vue'
import Vuex from 'vuex'
import createPersistedState from "vuex-persistedstate"
Vue.use(Vuex)

export default new Vuex.Store({
	plugins: [createPersistedState()],
  state: {
			user_id:'',
			list:[]
  },
  //	getters	类似state的计算属性；相当于一个二次处理
  getters:{
  	
  },
  mutations: {
			user_id(state,res){
						state.user_id = res.data.id
						localStorage.setItem('user_id', JSON.stringify(state.user_id))
			},
			singOut(state){
				window.localStorage.removeItem('user_id');
			},
			jia(state,key){
				state.list[key].num++
			},
			jian(state,key){
			state.list[key].num--
			},
			del(state,key){
			state.list.splice(key,1)
			},
			add(state,item){
			state.list.push({name:item.name,price:item.specfoods[0].price,num:1})
			}
			
  },
  actions: {

  }
})
