<template>
	<div class="box">
		<Headers  :FlagShowb=true :Magea=this.mage ></Headers>
		<div class="content">
			<div class="item">
				<el-input v-model="inputs" placeholder="输入学校，商务楼，地址" class="inp" clearable> ></el-input>
				<el-button type="primary" @click="search()" class="but">提交</el-button>
			</div>
			<div class="history"  :style="some">
				<div class="search">
					搜索历史
				</div>
				<div class="regions" v-for="(item,index) in info" :key="index" @click="getParams(item)">
					<p><b>{{item.name}}</b></p>
					<p>{{item.address}}</p>
				</div>
				<el-button type="primary" class="prim" @click="clear">清除历史</el-button>
				<div class="region" v-for="(item,index) in list" :key="index" @click="getParams(item)">
					<p><b>{{item.name}}</b></p>
					<p>{{item.address}}</p>
				</div>
			</div>
			<div class="history">
				<!--<div @click="bloc">-->
				<div class="region" v-for="(item,index) in list" :key="index" @click="getParams(item)">
					<p><b>{{item.name}}</b></p>
					<p>{{item.address}}</p>
				</div>
				<div class="bbb">{{ this.pagin }}</div>
				<!--</div>-->
			</div>
		</div>
	</div>
</template>

<script>
	function rem() {
		document.documentElement.style.fontSize = document.documentElement.clientWidth / 7.5 + "px";
	}
	rem();
	window.onresize = rem;
	import axios from 'axios'
	import Headers from '@/components/Headers.vue'
	export default {
		components: {
			Headers,
		},
		data() {
			return {
				inputs: '',
				list: [],
				mage: "",
				info: [],
				pagin: "",
				some: {
					"display": "block"
				}
			}
		},

		methods: {
			clear() {
				this.info = []
				if(this.info = []) {
					window.localStorage.removeItem('batas')
				}
			},
			search() {

				if(this.inputs) {
					axios.get('http://elm.cangdu.org/v1/pois?type=search&city_id=' + this.$route.params.id + '&keyword=' + this.inputs).then((res) => {
						this.list = res.data
						console.log(this.$route.params.id)
					})
					if(this.list.length != 0) {
						this.some = {
							"display": "block"
						}
					} else {
						this.some = {
							"display": "none"
						}
					}
				}
			},
			getParams(item) {
				this.$router.push({
					path: `/home?geohash=${item.geohash}`
				})
				localStorage.setItem('geohash', JSON.stringify(this.$router.history.current.query.geohash))
				var comment = {
					name: item.name,
					address: item.address,
					geohash: item.geohash
				}
				var arr = JSON.parse(localStorage.getItem("batas") || '[]')
				let obj = [...this.info]
				let zhi = obj.some((v) => {
					return v.geohash == item.geohash
				})
				if(zhi == false) {
					arr.push(item)
				}
				localStorage.setItem('batas', JSON.stringify(arr))
			},
		},

		mounted() {
			axios.get(`http://elm.cangdu.org/v1/cities/${this.$route.params.id}`).then((res) => {
					this.mage = res.data
				}),
				this.info = JSON.parse(localStorage.getItem('batas'))||[]
		}
	}
</script>
