			.inp {
				width: 90%;
				margin: 0.3rem 5%;
			}
			.but {
				width: 90%;
				margin: 0.1rem 5%;
			}
		}
		.history {
			width: 100%;
			.search {
				width: 6.5rem;
				height: 1rem;
				line-height: 1rem;
				padding: 0 0.5rem;
				border-bottom: 0.001rem solid rgb(228, 206, 206);
			}
		}
		.region {
			overflow: auto;
			width: 100%;
			height: 1.8rem;
			border-bottom: 0.001rem solid gainsboro;
